<template>
  <div>
    <barcode
      :width="this.format.width"
      :height="this.format.height"
      v-bind:value="this.product.code"
    >Show this if the rendering fails.</barcode>
  </div>
</template>

<script>
import Vue from "vue";

import axios from "axios";
import VueBarcode from "vue-barcode";

export default {
  name: "Barcode",
  components: {
    barcode: VueBarcode
  },
  data() {
    return {
      product: undefined,
      format: { height: "400", width: "5" },
      token:
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..DmWjursWGG4qKuW5sBUSC2XcMXk_vvUO2KGT4xMTNEc" // brand: "5bf4456457cf55001f0ebb0e",
    };
  },
  beforeMount() {
    this.getProduct();
  },
  methods: {
    async getProduct() {
      const resp = await axios.get(
        `https://parkcurity.herokuapp.com/products/${
          this.$route.params.id
        }?bbtoken=${this.token}`
      );
      this.product = resp.data.data;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



